<template>
  <div id="app" class="outer-container">
    <!-- Logo -->
    <div class="logo-container">
      <img src="@/assets/img/logo.png" alt="logo" class="logo-image" />
      <span class="logo-text"></span>
    </div>
    <!-- Order status display -->
    <div v-if="hasOrders" class="order-status-display">
      <div v-if="pendingOrders.length" class="order-status pending">
        : <span>{{ pendingOrders.length }}</span>
        <div class="order-list">
          <div v-for="order in pendingOrders" :key="order.orderId" class="customer-name">{{ order.customerName }}</div>
        </div>
      </div>
      <div v-if="inProgressOrders.length" class="order-status in-progress">
        : <span>{{ inProgressOrders.length }}</span>
        <div class="order-list">
          <div v-for="order in inProgressOrders" :key="order.orderId" class="customer-name">{{
              order.customerName
            }}
          </div>
        </div>
      </div>
      <div v-if="completedOrders.length" class="order-status completed">
        : <span>{{ completedOrders.length }}</span>
        <div class="order-list">
          <div v-for="order in completedOrders" :key="order.orderId" class="customer-name">{{
              order.customerName
            }}
          </div>
        </div>
      </div>
    </div>
    <!--  -->
    <div class="qr-code-container" v-show="qrCodeVisible">
      <div class="qr-title">
        <el-avatar :size="25" src="https://empty" @error="errorHandler">
          <img src='../assets/img/wechat-logo.png'/>
        </el-avatar>
        
      </div>
      <div class="orderId"><span style="color:red;margin-bottom: 10px;">{{ orderId }}</span></div>
      <div id="qrcode" style="width: 120px; height: 120px;"></div>
    </div>
    <div class="container">
      <el-container>
        <!-- <el-header class="center-content">  </el-header> -->
        <el-main>
          <el-form :model="form" label-width="220px">
            <el-form-item label="">
              <el-radio-group v-model="form.selectedRecipe">
                <el-radio-button :value="''"></el-radio-button>
                <el-radio-button :value="''"></el-radio-button>
              </el-radio-group>
            </el-form-item>
            <el-form-item label="">
              <el-radio-group v-model="form.selectedPrice">
                <el-radio-button v-for="price in prices" :key="price" :label="price">{{ price }}</el-radio-button>
              </el-radio-group>
            </el-form-item>
            <el-form-item label="">
              <el-radio-group v-model="form.selectedSpice">
                <el-radio-button v-for="spice in spices" :key="spice" :label="spice">{{ spice }}</el-radio-button>
              </el-radio-group>
            </el-form-item>
            <el-form-item label="">
              <el-radio-group v-model="form.addCilantro">
                <el-radio-button :value="true"></el-radio-button>
                <el-radio-button :value="false"></el-radio-button>
              </el-radio-group>
            </el-form-item>
            <el-form-item label="">
              <el-radio-group v-model="form.addOnion">
                <el-radio-button :value="true"></el-radio-button>
                <el-radio-button :value="false"></el-radio-button>
              </el-radio-group>
            </el-form-item>
            <el-form-item label="">
              <el-radio-group v-model="form.paymentMethod">
                <el-radio-button v-for="method in paymentMethods" :key="method.key" :label="method.key">
                  {{ method.label }}
                </el-radio-button>
              </el-radio-group>

            </el-form-item>
          </el-form>
          <div class="button-container">
            <el-button type="primary" @click="submitOrder" class="center-button">
            </el-button>
          </div>
        </el-main>
      </el-container>
    </div>
    <div class="go-to-backend">
      <el-icon :size="40" @click="goToBackend">
        <setting/>
      </el-icon>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
import QRCode from 'qrcodejs2';


import {Setting} from '@element-plus/icons-vue'

const baseUrl = 'http://127.0.0.1:8080/orders';
export default {
  components: {
    // ElIcon,
    Setting
  },
  name: 'App',
  data() {
    return {
      form: {
        selectedRecipe: '',
        selectedPrice: 20,
        selectedSpice: '',
        addCilantro: true,
        addOnion: true,
        paymentMethod: 'wechat'   // 
      },
      recipes: ['', ''],
      prices: [10, 15, 20, 30],
      spices: ['', '', '', ''],
      orderSubmitted: false,
      isButtonEnabled: false,
      paymentMethods: [
        {key: 'wechat', label: '', colorDark: "#000000", colorLight: "#07C160"},
        {key: 'alipay', label: '', colorDark: "#FFFFFF", colorLight: "#10AEFF"}
      ],
      serverStatus: {
        color: 'black',
        message: ''
      },
      pendingOrders: [],       // 
      inProgressOrders: [],    // 
      completedOrders: [],     // 
      hasOrders: false,         // 

      errorHandler: null, //  errorHandler  data 

      qrCodeDialogTitle: "", // 
      qrCodeVisible: false,    // 
      qrCodeText: '',               // url
      orderId: 'A0000',
      payMethods: ''    //
    };
  },
  methods: {
    // 
    generateQrCode() {
      // 
      const qrcodeElement = document.getElementById('qrcode');
      if (qrcodeElement) {
        qrcodeElement.innerHTML = ''; // 
        // 
        new QRCode(qrcodeElement, {
          text: this.qrCodeText,
          width: 120, // QR
          height: 120, // QR
          colorDark: "#000000", // QR
          colorLight: "#ffffff", // 
          correctLevel: QRCode.CorrectLevel.H // L, M, Q, H
        });
      }
    },
    /*userToken*/
    cleanSession() {
      localStorage.removeItem('userToken');
    },
    /**/
    goToBackend() {
      this.$router.push('/login'); // 
    },

    async submitOrder() {
      if (this.form.selectedRecipe && this.form.selectedPrice && this.form.selectedSpice) {
        try {
          const response = await axios.post(baseUrl, this.form, {
            headers: {
              'Content-Type': 'application/json'
            }
          });
          this.orderSubmitted = true;
          // this.$message.success('');
          // setTimeout(() => {
          //   location.reload();
          // }, 5000); // 5
        } catch (error) {
          this.$message.error('');
          console.error(error);
        }
      } else {
        this.$message.error('');
      }
    },
    async fetchServerStatus() {
      try {
        const response = await axios.get(baseUrl + '/serverStatus');
        this.serverStatus.color = response.data.color;
        this.serverStatus.message = response.data.message;
        // 
        this.isButtonEnabled = (this.serverStatus.color === 'green');
      } catch (error) {
        console.error(':', error);
        this.isButtonEnabled = false; // 
      }
    },
    async fetchOrderData() {
      try {
        const response = await axios.get(baseUrl + '/status');
        if (response.data.code === 200) {
          this.pendingOrders = response.data.data.pendingOrders || [];
          this.inProgressOrders = response.data.data.inProgressOrders || [];
          this.completedOrders = response.data.data.completedOrders || [];

          //  hasOrders 
          this.hasOrders = this.pendingOrders.length > 0 || this.inProgressOrders.length > 0 || this.completedOrders.length > 0;
        } else {
          console.error(response.data.message);
        }
      } catch (error) {
        console.error(':', error);
      }
    },
    //
    async fetchQrData() {
      try {
        const response = await axios.get(baseUrl + '/qrcode');
        if (response.data.code === 200 & response.data.data !== null) {
          this.qrCodeText = response.data.data.qrCodeText;
          console.log("response.data.data.qrCodeText===" + response.data.data)
          this.orderId = response.data.data.orderId;
          this.payMethods = response.data.data.payMethods;
          this.generateQrCode();
          this.qrCodeVisible = true;
        } else {
          this.qrCodeVisible = false;
          console.error(response.data.message);
        }
      } catch (error) {
        this.qrCodeVisible = false;
        console.error(':', error);
      }
    },
    //websocket
    connectWebSocket() {
      this.ws = new WebSocket("ws://localhost:8080/ws");
      console.log("websocket")
      this.ws.onmessage = (event) => {
        // console.log(event.data)
        if (event.data === 'qrSuccess') {
          this.fetchQrData();
        }
        if (event.data === 'paySuccess') {
          this.qrCodeVisible = false;
          //
          this.fetchOrderData();
          this.$message.success('')
        }
        if (event.data === 'getKeySuccess') {
          this.$message.success("");
        }
        if (event.data === 'failedToRetrievePassword') {
          this.$message.error(",");
        }
      };

      this.ws.onopen = () => {
        console.log("WebSocket");
      };
      this.ws.onclose = () => {
        console.log("WebSocket");
      };
    }
  },
  mounted() {
    //websocket
    this.connectWebSocket();
    /*session*/
    this.cleanSession();
    this.fetchServerStatus();
    this.fetchOrderData();
    setInterval(this.fetchServerStatus, 1000);
    setInterval(this.fetchOrderData, 1000);
  }
};
</script>


<style>
.logo-container {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 20px;
}

.logo-image {
  width: 60px; /* logo */
  height: 60px;
  border-radius: 50%; /*  */
  object-fit: cover; /*  */
  border: 2px solid #07C160; /* logo */
}

.logo-text {
  font-size: 24px;
  font-weight: bold;
  color: #333;
  margin-left: 10px;
}


html, body {
  height: 100%;
  margin: 0;
  background-color: var(--beige);
}

.outer-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  flex-direction: column;
}

.container {
  max-width: 600px;
  width: 100%;
  padding: 20px;
  font-family: 'Arial', sans-serif;
}

.button-container {
  text-align: center;
  margin-top: 20px;
}

.center-button {
  width: 50%;
  background-color: var(--deep-blue);
  color: #fff;
}

.el-main {
  padding: 20px;
}

.el-form-item {
  margin-bottom: 40px;
}

.el-form-item label {
  font-size: calc(8px + 1.5vh);
  color: rgb(72, 8, 25);
}

.el-radio-group {
  font-size: calc(12px + 1.5vh);
}

.el-button {
  height: 40px;
  font-size: calc(12px + 1.5vh);
  margin-top: 50px;
}

.order-details {
  margin-top: 20px;
  font-size: calc(12px + 1.5vh);
  color: rgb(72, 8, 25);
}

/*.status-message {*/
/*  width: 100%;*/
/*  padding: 20px;*/
/*  text-align: center;*/
/*  background-color: var(--gray);*/
/*  font-size: calc(12px + 1.5vh);*/
/*  position: fixed;*/
/*  bottom: 0;*/
/*  left: 0;*/
/*}*/

.order-status-display {
  position: absolute;
  top: 0;
  width: 100%;
  text-align: center;
  padding: 10px 0;
  background-color: var(--gray);
}

.order-status {
  font-size: calc(12px + 1.5vh);
  margin: 5px 0;
}

.order-status span {
  font-weight: bold;
  margin-left: 5px;
}

.order-list {
  display: grid;
  grid-template-columns: repeat(6, 1fr); /* 6 */
  gap: 5px;
  justify-content: center;
  max-width: 1000px; /* Set a max width to control the layout */
  margin: 0 auto;
}

.order-list .customer-name {
  text-align: center;
  font-size: calc(12px + 1.5vh);
}

.pending .customer-name {
  color: orange;
}

.in-progress .customer-name {
  color: var(--deep-blue);
}

.completed .customer-name {
  color: green;
}

/**/
.go-to-backend {
  position: fixed;
  bottom: 10px;
  right: 10px;
}

.go-to-backend .el-button {
  background-color: var(--deep-blue);
  color: var(--silver);
  margin-bottom: 50px;
}

.el-dialog__header {
  text-align: center;
}

.el-dialog__body img {
  display: block;
  margin: 0 auto;
  width: 100%;
  height: 100%;
}

.qr-code-container {
  position: fixed;
  bottom: 20px;
  left: 20px;
  width: 150px;
  height: 200px;
  background-color: white;
  border: 3px solid #07C160;
  border-radius: 10px;
  z-index: 1000;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin-bottom: 130px;
}

.qr-title {
  display: flex;
  align-items: center; /*  */
  justify-content: center; /*  */
  font-size: 20px;
  height: 25px;
  color: #07C160;
  margin-bottom: 5px;
}


</style>
